import styled from 'styled-components';

export const List = styled.ul`
  display: flex;
  flex-direction: row;
  gap: 2px;
  padding-left: 1.5px;
`;

export const ListItem = styled.li`
  padding: 2px;
  margin-bottom: 1px;
  border: solid 1px;
  width: 130px;
  text-align: center;
`;
